<template>
  <div class="register">
    <h2>Register</h2>
    <form @submit.prevent="handleSubmit">
      <div class="form-group">
        <label htmlFor="username">用户名</label>
        <input type="text" v-model="username" name="username" class="form-control" :class="{ 'is-invalid':!username }" />
        <div v-show="!username" class="invalid-feedback">请输入用户名</div>
      </div>
      <div class="form-group">
        <label htmlFor="password">口令</label>
        <input type="password" v-model="password" name="password" class="form-control" :class="{ 'is-invalid': !password }" />
        <div v-show="!password" class="invalid-feedback">请输入口令</div>
      </div>
      <div class="form-group">
        <label htmlFor="password">重复口令</label>
        <input type="password" v-model="password2" name="password2" class="form-control" :class="{ 'is-invalid': !password2 }" />
        <div v-show="!password2" class="invalid-feedback">请输入重复口令</div>
      </div>
      <div id="register">
        <input type="radio" id="admin" value="ROLE_ADMIN" v-model="role"> 我是教师
        <input type="radio" id="user" value="ROLE_USER" v-model="role"> 我是学生
      </div>
      <div class="form-group">
        <button class="btn btn-primary">注册</button>
      </div>
    </form>
  </div>
</template>

<script>
import {register} from '@/service/genServ.js'

export default {
  name: "Register",
  data(){
    return{
      username:'',
      password:'',
      password2:'',
      role:'ROLE_USER',
    }
  },
  methods: {
    handleSubmit() {
      if(this.password!==this.password2){
        this.$message({
          message:  '口令不一致',
          type: 'warnning',
        })
      }else{
        register({'username':this.username,'password':this.password,'role':this.role}).then(response=>{
          if (response.code === '0') {
            this.$message({
              message:  '成功',
              type: 'warnning',
            })
            this.$router.go(-1)
          } else {
            this.$message({
              message: response.msg,
              type: 'warnning',
            })
          }
        })
      }
    }
  }
}
</script>

